:root {
    --primary: #ff7648;
    --primarylight: #ffe1de;
    --white: #ffffff;
    --secondary: #3eb5d4;
    --secondarylight: #b1eeff;
    --black: #2d2d2d;
    --dark: #161616;
    --brown: #5d5151;
    --lightbrown: #bcabab;
    --lightbrown2: #f3ecec;
    --bg: #ffffff;
    --bg-light: #fff2f1;
}

* {
    box-sizing: border-box;
}

p {
    margin: 0;
    font-family: 'Quicksand', sans-serif;
    color: #5d5151;
    color: var(--brown);
    
}

html {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background-color: #ffffff;
    background-color: var(--bg);
    margin: 0;
    padding: 0;
}

a {
    font-family: 'Quicksand', sans-serif;
    color: #ff7648;
    color: var(--primary);
    text-decoration: none;
    text-transform: lowercase;
    font-weight: lighter;
}

a:hover {
    color: #3eb5d4;
    color: var(--secondary);
}

h1 {
    font-family: "Inter", sans-serif;
    color: #2d2d2d;
    color: var(--black);
    text-transform: uppercase;
    font-weight: bolder;
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 0;
    display: inline;
}

h3 {
    font-family: 'Quicksand', sans-serif;
    color: #2d2d2d;
    color: var(--black);
    /*text-align: left;*/
    font-size: 26px;
    margin: 5px 0;
    /*background-image: linear-gradient(transparent 0,transparent 55%,var(--secondarylight) 55%,var(--secondarylight) 90%,transparent 90%,transparent 100%);*/
}

strong {
    color: #3eb5d4;
    color: var(--secondary);
}

@media (max-width: 400px) {
    h1 {
        font-size: 26px;
    }
}



